<template>
	<div>
		<v-btn
			depressed
			color="primary"
			class="mb-6"
			@click="$router.push({name: 'create-traction'})">
			create traction
			<v-icon class="ml-3">mdi-plus</v-icon>
		</v-btn>
		<v-card>
			<v-card-title class="py-2">Startup</v-card-title>
			<v-card-text>
				<Loader v-if="isLoading" />
				<v-data-table
					v-else
					class="mt-0"
					:headers="header"
					:items="dataTraction"
					:items-per-page="10">	
					<template v-slot:item.no="{item}">
						{{dataTraction.map(x => x.id).indexOf(item.id)+1}}
					</template>
					<template v-slot:item.response="{item}">
						<v-btn
							depressed
							color="cyan"
							@click="$router.push({ name: 'startup-traction', params: { traction_id: item.id } })"
							dark small>
							lihat response
						</v-btn>
					</template>
					<template v-slot:no-data>
						<v-alert type="info" class="mt-4">
							<div>Data tidak ditemukan</div>
						</v-alert>
					</template>
					<template v-slot:item.action="{item}">
						<v-btn
							icon
							color="primary"
							@click="$router.push({ name: 'edit-traction', params: {traction_id: item.id} })">
							<v-icon small>mdi-pencil</v-icon>
						</v-btn>
						<HapusTraction :id="item.id" />
					</template>
				</v-data-table>
			</v-card-text>
		</v-card>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import Loader from '@/components/Loader'
	import HapusTraction from './HapusTraction'

	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Traction', value: 'judul', sortable: false },
				{ text: 'Date Release', value: 'created_at', sortable: false },
				{ text: 'Total Response', value: 'jumlah_jawaban', sortable: false },
				{ text: 'Lihat Response', value: 'response', sortable: false },
				{ text: 'Actions', value: 'action', sortable: false },
			]
		}),

		components: {
			HapusTraction,
			Loader
		},		

		computed: {
			...mapState('dataIncubatorTraction', ['dataTraction', 'isLoading'])
		}
	}
</script>